import React from 'react';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';

import './navigation.css';

import socialButtons from './socialButtons.js';


class NavigationBar extends React.PureComponent {
    renderTabs() {
        const {activeTab, tabs} = this.props;
        return tabs.map(tab =>
            <a
                href={'/' + tab.id}
                className={'nav-tab' + (activeTab === tab.id ? ' selected' : '')}
                key={tab.id}
                onClick={(e) => {
                    e.preventDefault();
                    this.handleTabChange(tab);
                }}
            >
                {tab.label}
            </a>
        )
    }

    handleTabChange(tab) {
        window.history.pushState(tab.id, '', './' + tab.id);
        this.props.onTabChange(tab.id);
    }

    renderSocialButtons() {
        const buttons = [];
        for (const [key, data] of Object.entries(socialButtons)) {
            buttons.push(
                <a key={key} href={data.url}>
                    <img
                    className='social-icon'
                    src={data.icon}
                    alt={key + ' social media icon'}
                    />
                </a>
            );
        }
        return buttons;
    }

    render() {
        const logo = (
            <div id='main-logo-container'>
                <a href='/' id='main-logo'>
                Samuel J. Li
                </a>
            </div>
        );
        const socialButtons = (
            <div id='social-buttons'>
            {this.renderSocialButtons()}
            </div>
        );

        return (
            <div id='navigation-bar'>
                <div id='main-bar'>
                    {logo}
                    <nav className='desktop navigation-tabs'>
                        {this.renderTabs()}
                    </nav>
                    {socialButtons}
                </div>
                <nav className='mobile navigation-tabs'>
                    {this.renderTabs()}
                </nav>
            </div>
        );
    }
}

export default NavigationBar;
